
import { defineComponent } from "@vue/runtime-core";
import VRuntimeTemplate from "vue3-runtime-template";
export default defineComponent({
    name: 'RenderContent',
    components: {
        VRuntimeTemplate
    },
    props: {
        content: String,
        id: Number
    },
    data: () => ({
        content2: '' as string
    }),
    created() {
        this.content2 = '<' + this.content + ' id="' + this.id + '" />'
    }
})
