
import { defineComponent } from "@vue/runtime-core";

export default defineComponent({
    name: 'ComponentPicker',
    data: () => ({
        all: "Table",
        allCharts: "",
        informational: ""
    }),
    emits: ["pickTemplate"],
    methods: {
        pickTemplate(layout: string){
            this.$emit('pickTemplate', {
                layout: layout
            })
        }
    }
})
